.wgpt3{
    display: flex;
    flex-direction: column;
    transition: all 0.3s;
    box-shadow: 0 0 20rem #839eab6e;
    padding: 2rem;
    background: var(--color-footer);

    /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.wgpt3:hover{
    
    transform: translate(0px, -10px);
}

.wgpt3__feature{
    display: flex;

}

.wgpt3__feature .wgpt3__container{
    margin: 0;
    
}
.wgpt3__feature .wgpt3__container-text{
    max-width: 700px;

}

.wgpt3__heading{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;
}

.wgpt3__heading h1{
    font-family: var(--font-family);
    font-size: 35px;
    font-weight: 800;
    line-height: 45px;
    max-width: 510px;

}

.wgpt3__heading p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
}

.wgpt3__container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}

.wgpt3__container .features__container-feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}
.features__container-feature__title h1{
    font-size: 20px;
}
/* Customizing feature component as per needs */
.wgpt3__container .features__container-feature__text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .wgpt3__heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .wgpt3__heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .wgpt3__feature .wgpt3__container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .wgpt3__feature .features__container-feature__text {
        margin-top: 0.5rem;
    }

    .wgpt3__heading h1{
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .wgpt3__container .features__container-feature{
        margin: 1rem 0;
        min-width: 100%;
    }
}